  methods: {
    ...formHelper,
    calculate () {
      if (this.payNormalTable.length > 0) {
        this.payNormalTable.forEach(v => {
          this.incomeAll += v.income
          this.mianTaxAll += v.incomeNoTax
        })
      }
    },
    submitForm (formName) {
      var _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let submitData = {} // 用于提交的主键，格式参考store
          if (!_this[formName].infoId) {
            _this[formName].infoId = _this.$store.getters.getFormInfoId(formName) + 1
          }
          _this[formName].incomeProject = '正常薪金所得'
          let monthNumber = _this.$store.state.service.monthNumber
          console.log(monthNumber)
          // 累计收入
          _this[formName].incomeAll = _this[formName].income * monthNumber
          // 累计免税收入
          _this[formName].incomeNoTaxAll = _this[formName].incomeNoTax * monthNumber
          // 累计减除费用
          _this[formName].otherAll.jianchufeiyongbiaozhunAll = _this[formName].otherAll.jianchufeiyongbiaozhun * monthNumber
          // 累计扣除及减除项目合计
          _this[formName].jianchuAll = (parseFloat(_this[formName].benqizhuanxiangxiaoji) + parseFloat(_this[formName].onlyForm.zhuanxiangkouchuxiaoji) + parseFloat(_this[formName].otherForm.benqikouchuxiaoji)) * monthNumber
          // 累计准予扣除的捐赠额
          _this[formName].otherAll.zhunyukouchuAll = _this[formName].otherAll.zhunyukouchu
          // 累计应纳税所得额 = 本期收入 - 本期免税收入 - 累计扣除项目合计 - 减除费用标准
          // _this[formName].leijiNashuiAll = ((parseFloat(_this[formName].income) - _this[formName].jianchuAll - parseFloat(_this[formName].otherAll.jianchufeiyongbiaozhun) - parseFloat(_this[formName].incomeNoTax) - parseFloat(_this[formName].otherAll.zhunyukouchu))) * monthNumber.toFixed(2)
          _this[formName].leijiNashuiAll = (parseFloat(_this[formName].incomeAll) - _this[formName].incomeNoTaxAll - parseFloat(_this[formName].otherAll.jianchufeiyongbiaozhunAll) - parseFloat(_this[formName].jianchuAll) - parseFloat(_this[formName].otherAll.zhunyukouchuAll)).toFixed(2)
          if (_this[formName].leijiNashuiAll <= 0) {
            _this[formName].leijiNashuiAll = 0
          }
          // 应纳税额
          _this[formName].taxAll = dataHelper.shuilvPercent(_this[formName].leijiNashuiAll)
          // _this[formName].taxAll.taxPay = dataHelper.shuilvEnd(_this[formName].leijiNashuiAll, monthNumber)
          // 累计减免税额
          _this[formName].otherAll.jianmianshuieAll = _this[formName].otherAll.jianmianshuie

          // 累计应扣缴税额 = 累计应纳税额 - 减免税额
          _this[formName].yingkoujiaoshuie = _this[formName].taxAll.taxPay - parseFloat(_this[formName].otherAll.jianmianshuie)
          // 应补(退)税额 = 累计应纳税额 - 减免税额
          _this[formName].tuibu = _this[formName].yingkoujiaoshuie - _this[formName].otherAll.yikoujiaoshuie
          submitData.formId = formName
          submitData.content = _this[formName]
          if (_this.isUpdata) {
            _this.$store.commit('updataFormInfo', submitData)
            _this.isUpdata = false
          } else {
            _this.$store.commit('addFormInfo', submitData)
          }

          _this.centerDialogVisible = false
          this[formName] = this.$options.data()[formName]
          // alert('提交成功!')
          _this.$message.success('提交成功!')
          this.calculate()
        } else {
          return false
        }
      })
    },
    benqizhuanxiang () {
      let b = parseFloat(this.normalForm.yanglaobaoxian) + parseFloat(this.normalForm.yiliaobaoxian) + parseFloat(this.normalForm.shiyebaoxian) + parseFloat(this.normalForm.zhufanggongjijin)
      this.normalForm.benqizhuanxiangxiaoji = b.toFixed(2)
    },
    leijizhuanxiang () {
      let b = parseFloat(this.normalForm.onlyForm.childEducation) + parseFloat(this.normalForm.onlyForm.houseInterest) + parseFloat(this.normalForm.onlyForm.houseRent) + parseFloat(this.normalForm.onlyForm.oldman) + parseFloat(this.normalForm.onlyForm.continueLearn)
      this.normalForm.onlyForm.zhuanxiangkouchuxiaoji = b
    },
    qitakouchu () {
      let b = parseFloat(this.normalForm.otherForm.yearIncome) + parseFloat(this.normalForm.otherForm.shangyebaoxian) + parseFloat(this.normalForm.otherForm.shuiyanbaoxian) + parseFloat(this.normalForm.otherForm.else)
      this.normalForm.otherForm.benqikouchuxiaoji = b
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
