    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
